@import '../design-tokens.css';

:root { --flow-horizontal-padding: var(--space-20); }

/* Wrapper contenitore flusso: centra e applica max-width condivisa */
.q-flow-container { max-width: 640px; margin: 0 auto; padding: 0 var(--flow-horizontal-padding); }

/* Header con stesso spazio orizzontale delle domande */
.q-flow-header { max-width: 640px; margin: 0 auto var(--space-32); }
.q-flow-header-inner { padding: 0 var(--flow-horizontal-padding); text-align: center; }
.q-flow-header h1 { font-size: var(--font-size-xl); margin: 0 0 var(--space-8); font-weight: var(--font-weight-bold); line-height: 1.2; }
.q-flow-header p { font-size: var(--font-size-base); margin:0 0 var(--space-12); color: var(--color-text-light); line-height: 1.8; }

.progress { margin-bottom: var(--space-24); }
.progress-bar-outer { background:#eee; height:12px; border-radius: var(--radius-pill); overflow:hidden; }
.progress-bar-inner { background: var(--color-primary); height:100%; transition: width .3s; }
.progress-text { font-size: var(--font-size-sm); margin-top: var(--space-6); font-weight: var(--font-weight-medium); }

.nav-row { display:flex; justify-content: space-between; margin-top: var(--space-16); gap: var(--space-12); }
.btn-back {
	display:inline-flex; align-items:center; justify-content:center; gap: var(--space-8);
	border:2px solid var(--color-primary);
	background: var(--color-surface); color: var(--color-primary);
	border-radius: var(--radius-md);
	font-family: var(--font-family-base);
	font-size: var(--font-size-base);
	font-weight: var(--font-weight-semibold);
	padding: var(--space-12) var(--space-20);
	cursor:pointer; transition: background .2s, color .2s;
}
.btn-back:hover { background: var(--color-primary); color: var(--color-primary-contrast); }

.completed-wrap .progress-bar-inner { width:100% !important; }